import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AppComponent } from './app.component';
import { AboutPageComponent } from './about-page/about-page.component';
import { NavbarComponent } from './navbar/navbar.component';
import { HomePageComponent } from './home-page/home-page.component';
import { ContactPagComponent } from './contact-pag/contact-pag.component';
import { PortfolioPageComponent } from './portfolio-page/portfolio-page.component';
import { PortfolioPageArticleComponent } from './portfolio-page-article/portfolio-page-article.component';
const appRoutes: Routes = [
  { path: '', component: HomePageComponent },
  { path: 'about',component: AboutPageComponent },
  { path: 'contact',component:ContactPagComponent },
  { path: 'portfolio',component:PortfolioPageComponent },
  { path: 'portfolio/:project',component:PortfolioPageArticleComponent },
  { path: '**',component:HomePageComponent }
];
@NgModule({
  declarations: [
    AppComponent,
    AboutPageComponent,
    NavbarComponent,
    HomePageComponent,
    ContactPagComponent,
    PortfolioPageComponent,
    PortfolioPageArticleComponent
  ],
  imports: [
    RouterModule.forRoot(
    appRoutes
  ),
    BrowserModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
