import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-portfolio-page-article',
  templateUrl: './portfolio-page-article.component.html',
  styleUrls: ['./portfolio-page-article.component.css']
})
export class PortfolioPageArticleComponent implements OnInit {
  currArticle:string = ""
  constructor(private router:Router) { }

  ngOnInit() {
    if(this.router.url.startsWith("/portfolio/")){
    switch (this.router.url) {
      case "/portfolio/fixit":  this.currArticle = "fixit";break;
      case "/portfolio/suim":  this.currArticle = "suim";break;
      case "/portfolio/gform":  this.currArticle = "gform";break;
      case "/portfolio/bolero":  this.currArticle = "bolero";break;
      case "/portfolio/facturia":  this.currArticle = "facturia";break;
      case "/portfolio/internspace":  this.currArticle = "internspace";break;
    }
        console.log(this.currArticle);
  }
}
  

}
